.list {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
}

.list li {
	display: block;
	list-style: none;
	padding: 20px;
    border: 1px solid #ddd;
    box-sizing: border-box;
    margin-right: 20px;
	margin-bottom: 20px;
}

input {
	width: 400px;
}

.container {
	max-width: 1000px;
	margin: 100px auto;
}